import FileUtil from "@/js/core/file-manager";
import Data from "@/js/core/data";
class QuillImageUpload extends Data {
  constructor() {
    super();
  }
  async upload(file, file_type, app) {
    let app_name = app ? app : "projects";
    let upload_response = await FileUtil.getSignedLink(`${app_name}`, "post-object", `${file.name}`, `${file_type}`, "public-read");
    // Converting file object into formData
    var formData = new FormData();
    Object.keys(upload_response.fields).forEach((field) => {
      formData.append(field, upload_response.fields[field]);
    });
    formData.append("file", file);
    this.url = upload_response.url;
    this.disablePCors();
    // Don't need Authorization token here
    super.headers = {
      Authorization: "",
      withCredentials: false,
    };
    await super.multi_call(formData);
    return upload_response;
  }
}
export default new QuillImageUpload();
