<script>
  import VueMomentsAgo from "@/components/VueMoments.vue";
  import AddFeedback from "./AddFeedback";
  import CommentDetails from "./CommentDetails";
  import { bus } from "@/js/core/emit-bus";
  import FeedbackUp from "@/js/feedbackup/feedbackup.js";
  import Votes from "@/js/feedbackup/votes";
  import FilterData from "./Filter";
  import FeedbackDetails from "./FeedbackDetails.vue";
  import Avatar from "vue-avatar";

  export default {
    components: {
      AddFeedback,
      CommentDetails,
      FilterData,
      VueMomentsAgo,
      FeedbackDetails,
      Avatar,
    },
    data() {
      return {
        count: 0, // Total feedbacks count
        showFeedbackData: [], // Showing feedback data based on page
        results_current_page: 1,
        loading_project: false,
        project_data: [],
        category_loading: true,
        projects_loading: true,
        votesData: [],
        visitor_name: "",
        loading: true,
        fIndex: 0,
        visitor_id: "0",
        rowData: {},
        fdata: [],
        feed_back_data: [],
        options: [
          { value: "", text: "All" },
          { value: "Open", text: "Open" },
          { value: "In Works", text: "In Works" },
          { value: "Planned", text: "Planned" },
          { value: "Close", text: "Closed" },
        ],
        topRatedProjects: [
          { name: "Feedbackup", count: "25" },
          { name: "Botup", count: "23" },
          { name: "Forms", count: "20" },
          { name: "BPMAPP", count: "15" },
          { name: "Mailsend", count: "15" },
        ],
        topFeedbacks: [],
        status: "",
        search: "",
        render: 0,
        frender: 0,
        form: {
          name: "",
          email: "",
        },
      };
    },
    watch: {
      // When change page
      results_current_page(after) {
        // Calling get page data
        this.getPageWiseData(after - 1);
      },
    },
    async mounted() {
      this.showFeedbackData = [];
      this.loading_project = false;
      this.loading = true;
      this.projects_loading = true;
      this.category_loading = true;
      this.topFeedbacks = [];
      this.status = "Open";
      this.getTopFeedbacks();
      await this.getProject();
      this.getTopRatedProjects();
      await this.getCookies();
      await this.getVotes();
      await this.getFeedbackCount(); // Count call
      await this.getFeedbacks();
      this.getPageWiseData(this.results_current_page - 1); // Get page wise data
      this.fdata = this.feed_back_data;
      bus.$on("submitFeedback", (response, selected) => {
        this.$refs.addFeedbackForm.btn_save = "Save";
        this.$root.$emit("bv::toggle::collapse", "AddFeedback");
        if (response.length == 1 && !selected) {
          this.updateUI(response[0].GENERATED_KEY, "POST");
        } else {
          this.updateUI(selected, "PUT");
        }
      });
    },
    methods: {
      // Get feedbacks count
      async getFeedbackCount() {
        var where = this.status ? `status=${this.status}` : "";
        console.log("where", where);
        let response = await FeedbackUp.getFeedbackCount(where);
        this.count = response[0].count;
      },
      // Assign data based on page
      async getPageWiseData(results_current_page) {
        // Check page data if is there directly assign other get from the database
        if (results_current_page * 50 + 1 > this.feed_back_data.length && this.feed_back_data > 0) {
          await this.getFeedbacks(this.feed_back_data.length);
        }
        this.showFeedbackData = this.feed_back_data.slice(results_current_page * 50, (results_current_page + 1) * 50);
      },
      async getProject() {
        try {
          this.loading_project = false;
          this.project_data = await FeedbackUp.getProject();
          this.loading_project = true;
        } catch (err) {
          this.project_data = [];
          this.loading_project = true;
        }
      },
      async sortBy(column, key) {
        this.loading = true;
        try {
          var sort_by = `order_by=${column} ${key}`;
          let where = this.status ? "status=" + this.status : "";
          let response = await FeedbackUp.getFilterData(where, sort_by);
          this.feed_back_data = response;
          this.getPageWiseData(0);
          this.results_current_page = 1;
          this.loading = false;
        } catch (err) {
          this.loading = false;
        }
      },
      feedbackDetails(feedback, ind) {
        this.fIndex = ind;
        this.rowData = feedback;
        this.frender++;
        this.$root.$emit("bv::toggle::collapse", "feedbackDetails");
      },
      editRow(data) {
        this.$refs.addFeedbackForm.form.title = data.title;
        this.$refs.addFeedbackForm.form.description = data.description;
        this.$refs.addFeedbackForm.form.type = data.type;
        this.$refs.addFeedbackForm.form.notify = data.notify ? true : false;
        this.$refs.addFeedbackForm.form.id = data.id;
        this.$root.$emit("bv::toggle::collapse", "AddFeedback");
      },
      async updateVote(type, data, ind) {
        // Update UI to change the object
        let obj = data;
        obj.votes =
          type == 1 ? parseInt(obj.votes.split(".")[0]) + 1 + "." + (parseInt(obj.votes.split(".")[1]) - 1) : parseInt(obj.votes.split(".")[0]) - 1 + "." + (parseInt(obj.votes.split(".")[1]) + 1);
        let form = {
          voted_by: this.visitor_id,
          feedback_id: data.id,
          is_upvoted: type,
          created_by: window.user, // Its need for activity
        };
        let self = this;
        self.votesData.filter((item, ind) => {
          if (item.feedback_id == data.id && item.voted_by.toString() == self.visitor_id) {
            form.id = item.id;
            item.is_upvoted = type;
            this.$set(this.votesData, ind, item);
          }
        });
        // Chenged object set to data
        this.$set(this.feed_back_data, ind, obj);
        try {
          await Votes.updateVote(form);
        } catch (e) {
          console.log(e);
        }
      },
      checkVote(key) {
        let self = this;
        var matchVote = self.votesData.filter((item) => {
          if (item.feedback_id == key && item.voted_by.toString() == self.visitor_id) return item;
        });
        return matchVote.length > 0 ? true : false;
      },
      async getVotes() {
        try {
          if (this.visitor_id != "0") this.votesData = await Votes.getVotesById(this.visitor_id);
        } catch (err) {
          console.log(err);
        }
      },
      async getTopFeedbacks() {
        try {
          this.topFeedbacks = await FeedbackUp.getTopFeedbacks();
          this.category_loading = false;
        } catch (err) {
          console.log(err);
          this.category_loading = false;
        }
      },

      async getTopRatedProjects() {
        try {
          this.topRatedProjects = await FeedbackUp.getTopRatedProjects();
          this.projects_loading = false;
        } catch (err) {
          this.topRatedProjects = [];
          this.projects_loading = false;
        }
      },
      async applyFilter(item) {
        if (item == {}) return this.getFeedbacks();
        var where = "";
        Object.keys(item).forEach((key) => {
          if (typeof item[key] == "object" && key == "created_date") {
            where = where
              ? where + `&startdate=${item[key].startDate.toJSON().split("T")[0]}&enddate=${item[key].endDate.toJSON().split("T")[0]}`
              : `startdate=${item[key].startDate.toJSON().split("T")[0]}&enddate=${item[key].endDate.toJSON().split("T")[0]}`;
          } else if (typeof item[key] == "object" && key == "votes") {
            if (item[key].votes_count.trim() != "")
              where = where ? where + " and votes_count" + item[key].votes_condition + item[key].votes_count : "votes_count" + item[key].votes_condition + item[key].votes_count;
          } else if (item[key].trim() != "") {
            where = where ? where + "&" + key + "=" + item[key] : key + "=" + item[key];
          }
        });
        this.$root.$emit("bv::toggle::collapse", "filter-collapse");
        if (where.trim() == "") return this.getFeedbacks();
        this.loading = true;
        let response = await FeedbackUp.getFilterData(where);
        this.feed_back_data = response;
        this.fdata = this.feed_back_data;
        this.loading = false;
      },
      async getFeedbacks(offset = 0) {
        this.loading = true;
        let response = [];
        try {
          if (!this.status) response = await FeedbackUp.getFeedback(offset);
          else response = await FeedbackUp.getFilterData(`status=${this.status}`, "", offset);
          this.loading = false;
          if (offset == 0) {
            this.feed_back_data = response;
            this.getPageWiseData(0);
            this.results_current_page = 1;
          } else this.feed_back_data = [...this.feed_back_data, ...response];
          this.fdata = response;
        } catch (err) {
          this.loading = false;
        }
      },
      // Update UI
      async updateUI(id, type) {
        let response = await FeedbackUp.getDetails(id, "feedback");
        if (type == "POST") {
          this.feed_back_data.unshift(response[0]);
          this.showFeedbackData.unshift(response[0]);
        } else {
          let index = this.feed_back_data.findIndex((item) => {
            return item.id == response[0].id;
          });
          let show_feedback_index = this.showFeedbackData.findIndex((item) => {
            return item.id == response[0].id;
          });
          if (index >= 0) {
            this.$set(this.feed_back_data, index, response[0]);
            this.$set(this.showFeedbackData, show_feedback_index, response[0]);
          }
        }
        this.fdata = this.feed_back_data;
        // Update categories
        this.getTopFeedbacks();
      },
      async onSubmit() {
        let response = await FeedbackUp.addVisitor(this.form);
        this.visitor_id = response[0].GENERATED_KEY;
        this.visitor_name = this.form.name;
        this.$bvModal.hide("visitor");
        this.sendVote("1", this.rowData, this.fIndex);
      },
      async deleteVote(type, data, index) {
        let obj = data;
        obj.votes = parseInt(obj.votes) - 1;
        let form = {
          voted_by: this.visitor_id,
          feedback_id: data.id,
          is_upvoted: type,
          created_by: window.user, // Its need for activity
        };
        let self = this;
        let votes_id;
        let vote_index;
        self.votesData.findIndex((item, ind) => {
          if (item.feedback_id == data.id && item.voted_by.toString() == self.visitor_id) {
            votes_id = item.id;
            vote_index = ind;
            form.id = item.id;
          }
        });
        this.$set(this.feed_back_data, index, obj);
        try {
          await Votes.deleteVotes(form.feedback_id, votes_id);
          this.votesData.splice(vote_index, 1);
        } catch (e) {
          obj.votes = parseInt(obj.votes) + 1;
          this.$set(this.feed_back_data, index, obj);
          console.log(e);
        }
      },
      async sendVote(type, data, ind) {
        if (this.visitor_id == "0") {
          this.rowData = data;
          this.fIndex = ind;
          this.$bvModal.show("visitor");
        } else {
          let obj = data;
          obj.votes = parseInt(obj.votes) + 1;
          let form = {
            voted_by: this.visitor_id,
            feedback_id: data.id,
            is_upvoted: type,
            domain_id: window.domain,
            created_by: window.user, // Its need for activity
          };
          this.votesData.unshift(form);
          this.$set(this.feed_back_data, ind, obj);
          try {
            let response = await Votes.addVote(form);
            let UpdateData = await Votes.getDetails(response[0].GENERATED_KEY, form.feedback_id);
            this.$set(this.votesData, 0, UpdateData[0]);
            this.$set(this.feed_back_data, ind, obj);
          } catch (e) {
            console.log(e);
          }
        }
      },
      async searchFeedback(key, column) {
        if (column == "title") {
          if (key.trim().length == "" && this.status == "") return this.getFeedbacks();
          if (key.trim().length > 2) {
            let where = this.status == "" ? `title=${key}` : `status=${this.status}&title=${key}`;
            this.loading = true;
            let response = await FeedbackUp.getFilterData(where);
            this.feed_back_data = response;
            this.loading = false;
          }
        } else {
          this.search = "";
          await this.getFeedbackCount();
          if (key == "") return this.getFeedbacks();
          let where = `status=${key}`;
          this.loading = true;
          let response = await FeedbackUp.getFilterData(where);
          this.feed_back_data = response;
          this.getPageWiseData(0);
          this.results_current_page = 1;
          this.loading = false;
        }
      },
      addFeedback() {
        this.render++;
        this.$refs.addFeedbackForm.render++;
        this.$root.$emit("bv::toggle::collapse", "AddFeedback");
      },
      showComments(data, ind) {
        this.fIndex = ind;
        this.rowData = data;
        this.render++;
        this.$root.$emit("bv::toggle::collapse", "commentDetails");
      },
      getCookies() {
        // Get visitor id from cookie if not exist in response.
        let visitor = document.cookie.split(";").filter((res) => {
          return res.includes("FVID=");
        });
        if (visitor.length) {
          this.visitor_id = visitor[0].split("FVID=")[1];
        }

        // Get name from cookie if not exist in response.
        let name = document.cookie.split(";").filter((res) => {
          return res.includes("fname=");
        });
        if (name.length) {
          this.visitor_name = name[0].split("fname=")[1];
        }
      },
    },
    destroyed() {
      bus.$off("submitFeedback");
    },
  };
</script>
