<template>
  <div>
    <quill-editor ref="quill_editor_ref" v-model="description" :options="editorOptions" @blur="onEditorBlur($event)" @focus="onEditorFocus($event)" @ready="onEditorReady($event)" />
  </div>
</template>
<script>
  import { quillEditor, Quill } from "vue-quill-editor";
  import { ImageUpload } from "quill-image-upload";
  import "quill/dist/quill.core.css";
  import "quill/dist/quill.snow.css";
  import "quill/dist/quill.bubble.css";
  import Delta from "quill-delta";
  import QuillUpload from "@/js/feedbackup/quill-upload.js";
  import "@/js/feedbackup/quill-attachment.js";
  import MagicUrl from "quill-magic-url";
  Quill.register("modules/magicUrl", MagicUrl);
  Quill.register("modules/imageUpload", ImageUpload);
  export default {
    props: {
      ref_data: String,
      value: String,
    },
    components: { quillEditor },
    data() {
      return {
        description: "",
        editorOptions: {
          modules: {
            toolbar: [
              ["bold", "italic", "underline", "strike", "link", "image"], // toggled buttons
              [{ list: "ordered" }, { list: "bullet" }],
              ["files"],
            ],
            imageUpload: {
              customUploader: () => {
                this.uploadImage(this.selectedFile);
              },
              // add callback when a image have been chosen
              checkBeforeSend: (file, next) => {
                this.selectedFile = file;
                next(file); // go back to component and send to the server
              },
            },
            magicUrl: {
              normalizeUrlOptions: {
                stripHash: false, // Strip the hash part of the URL.
                stripWWW: false, // Remove www. from the URL.
                normalizeProtocol: true,
              },
            },
          },
          selectedFile: "",
          placeholder: "Enter your comment here....",
          theme: "snow",
        },
      };
    },
    mounted: function() {
      this.description = this.value;
    },
    methods: {
      async uploadImage(image_url) {
        let file_type = image_url.type;
        let app = "feedbackup";
        let upload_response = await QuillUpload.upload(image_url, file_type, app);
        let currentIndex = this.$refs.quill_editor_ref.quill.selection.lastRange ? this.$refs.quill_editor_ref.quill.selection.lastRange.index : 0;
        let editor_ref = this.$refs.quill_editor_ref.quill;
        // Add link to image
        editor_ref.updateContents(
          // Create new delta object
          new Delta()
            // retain previous text
            .retain(currentIndex)
            .retain(1, { link: `${upload_response.url}${upload_response.fields.key}` }),
        );
        this.$refs.quill_editor_ref.quill.insertEmbed(currentIndex, "image", `${upload_response.url}${upload_response.fields.key}`);
      },
      onEditorReady(quill) {
        var files_button = quill.getModule("toolbar").container.querySelector(".ql-files");
        if (files_button) {
          files_button.addEventListener("click", () => {
            this.selectLocalImage();
          });
        }
      },
      // Emit on editor blur
      onEditorBlur(quill) {
        this.$emit("blur", quill);
        this.$emit("quill_close");
      },
      // Emit on editor focus
      onEditorFocus(quill) {
        this.$emit("focus", quill);
      },
    },
  };
</script>
<style>
  .image {
    width: 500px;
    height: 200px;
  }
</style>
