import Data from "@/js/core/data";
const Votes_URL = "https://api.${region}.500apps.com/feedbackup/";

class Votes extends Data {
  // Get votes based on Visitor id
  getVotesById(id) {
    let data = new Data(`${Votes_URL}${window.domain}/${window.project_id}/${id}/visitor-votes`);
    data.disablePCors();
    return data.get();
  }

  // Get votes based on id
  getDetails(id, feedback_id) {
    let data = new Data(`${Votes_URL}${window.domain}/${window.project_id}/${feedback_id}/votes`);
    data.params = `id=${id}`;
    data.disablePCors();
    return data.get();
  }

  // Add vote
  addVote(form) {
    let data = new Data(`${Votes_URL}${window.domain}/${window.project_id}/${form.feedback_id}/votes`);
    data.disablePCors();
    return data.multi_call(form);
  }

  // Update vote
  updateVote(form) {
    let data = new Data(`${Votes_URL}${window.domain}/${window.project_id}/${form.feedback_id}/${form.id}/votes`);
    data.disablePCors();
    return data.edit(form.id, form);
  }
  deleteVotes(feedback_id, vote_id) {
    let data = new Data(`${Votes_URL}${window.domain}/${window.project_id}/${feedback_id}/${vote_id}/votes`);
    data.disablePCors();
    return data.delete(vote_id);
  }
}

export default new Votes();
