import { Quill } from "vue-quill-editor";
const Embed = Quill.import("blots/embed");
class AttachmentBlot extends Embed {
  static create(value) {
    let node = super.create();
    typeof value == "string" ? node.setAttribute("src", value) : node.setAttribute("src", value.url);
    node.setAttribute("target", "_blank");
    node.innerText = value.text;
    node.addEventListener(
      "click",
      () => {
        value.callback(value);
      },
      false,
    );
    return node;
  }
  // let value be hash containing both href and text
  static value(node) {
    return { url: node.getAttribute("href"), text: node.innerText };
  }

  static formats(node) {
    return node.getAttribute("href");
  }
}

AttachmentBlot.blotName = "attachment";
AttachmentBlot.tagName = "A";
AttachmentBlot.className = "attachment";

class ImageBlot extends Embed {
  static create(value) {
    let node = super.create();
    typeof value == "string" ? node.setAttribute("src", value) : node.setAttribute("src", value.url);
    node.setAttribute("target", "_blank");
    node.setAttribute("id", "quill-image");
    node.innerText = value.text;
    node.addEventListener(
      "click",
      () => {
        value.callback(value);
      },
      false,
    );
    return node;
  }

  static value(node) {
    return { url: node.getAttribute("src"), text: node.textContent };
  }

  static formats(node) {
    return node.getAttribute("src");
  }
}

ImageBlot.blotName = "image";
ImageBlot.tagName = "Img";
ImageBlot.className = "image";

Quill.register({
  "formats/attachment": AttachmentBlot,
  "formats/image": ImageBlot,
});
